<template>
  <div
    :style="{ backgroundImage: `url(${subBackgroundImageUrl})` }"
    class="subBackgroundImage"
  >
    
    <buct></buct>
    <div
      style="
        position: absolute;
        top:11vh;
        left: 73%;
        width: 40px;
        height: 1px;
        width: 120px;
        height: 60px;
        background: #7cb9dc;
        border-radius: 50% / 50%;
        color:#326277;
        font-size:20px;
        font-weight:bold;
        padding-top:15px;
      "
      class="menuHoverStyle"
      @click="changeTopic"
    >
      {{topic[k]}}
    </div>
    <div
      style="
        font-size: 50px;
        font-weight: bold;
        font-family: Tahoma;
        position: absolute;
        left: 5%;
        top: 5%;
      "
      class="menuHoverStyle"
      @click="returnIndex"
    >
      MENU
    </div>

    <div style="padding-top: 15vh; font-size: 25px; font-weight: bold">
      {{ this.name }}
    </div>
    <div style="width: 1100px; margin: 0 auto">
      <a-table
        :columns="columns"
        :data-source="data"
        :rowKey="'id'"
        v-show="isTableShow"
        :pagination="pagination"
        :rowClassName="rowClassName"
        size="middle"
        class="that"
      ></a-table>
    </div>
  </div>
</template>

<script>
import buct from "../components/common/buct";
const columns = [
  {
    title: "内容",
    dataIndex: "content",
    width: 375,
    ellipsis: true,
  },
  {
    title: "可信度",
    dataIndex: "confidenceCoefficient",
    width: 125,
  },
  {
    title: "积极概率",
    dataIndex: "positive",
    width: 125,
  },
  {
    title: "消极概率",
    dataIndex: "negative",
    width: 125,
  },
];
export default {
  name: "keywordAnalysisDetail",
  data() {
    return {
      subBackgroundImageUrl: "",
      isTableShow: false,
      data: [],
      all:[],
      positive:[],
      negative:[],
      neutral:[],
      pagination: { pageSize: 5 },
      name: this.$route.query.name,
      columns,
      k:0,
      topic:['全部','积极','中立','消极'],
    };
  },
  components: {
    buct,
  },
  methods: {
    changeTopic(){
      this.k=this.k+1
      if(this.k>3) this.k=0
      if(this.k==0){
        this.data = []
        for(let item of this.all){
          this.data.push(item)
        }
      }
      else if(this.k==1){
        this.data = []
        for(let item of this.positive){
          this.data.push(item)
        }
      }
      else if(this.k==2){
        this.data = []
        for(let item of this.neutral){
          this.data.push(item)
        }
      }
      else if(this.k==3){
        this.data = []
        for(let item of this.negative){
          this.data.push(item)
        }
      }
      
    },
    returnIndex() {
      this.$router.push("/mainMenu");
    },
    getBackendData() {
      this.$store.state.keywordAnalyzingDataGet(this.name).then((response) => {
        this.data = response.data;
        this.all = this.data
        this.isTableShow = true;
        for(let item of this.data){
          if(item.classification == 'positive'){
            this.positive.push(item)
          }
          else if(item.classification == 'negative'){
            this.negative.push(item)
          }
          else if(item.classification == 'neutral'){
            this.neutral.push(item)
          }
        }
      });
    },
    rowClassName(record, index) {
      let className = "even-row";
      if (index % 2 === 1) className = "odd-row";
      return className;
    },
  },
  mounted() {
    this.subBackgroundImageUrl = require("../assets/subBackgroundImage.png");
    this.getBackendData();
  },
};
</script>

<style scoped>
.subBackgroundImage {
  background-position: center top;
  background-repeat: no-repeat;
  width: 100vw;
  height: 100vh;
  background-size: 100vw 100vh;
}
.even-row {
  background-color: rgba(165, 166, 255, 100);
  height: 90px;
}
.odd-row {
  background-color: rgba(165, 208, 255, 100);
  height: 90px;
}
.menuHoverStyle:hover {
  cursor: pointer;
}

</style>