<template>
  <div
    :style="{ backgroundImage: `url(${subBackgroundImageUrl})` }"
    class="subBackgroundImage"
  >
    <buct></buct>
    <div
      style="
        font-size: 50px;
        font-weight: bold;
        font-family: Tahoma;
        position: absolute;
        left: 5%;
        top: 5%;
      "
      class="menuHoverStyle"
      @click="returnIndex"
    >
      MENU
    </div>

    <div style="width: 1100px; margin: 0 auto">
      <a-table
        v-show="isTableShow"
        :columns="columns"
        :data-source="data"
        :rowKey="'id'"
        :expandedRowKeys="expandedRowKeys"
        @expand="expand"
        size="middle"
        style="padding-top: 18vh"
        :pagination="pagination"
        :rowClassName="rowClassName"
        expandRowByClick
        class="this"
      >
        <!--<div
          slot="filterDropdown"
          slot-scope="{
            setSelectedKeys,
            selectedKeys,
            confirm,
            clearFilters,
            column,
          }"
          style="padding: 8px"
        >
          <a-input
            :placeholder="`Search ${column.dataIndex}`"
            :value="selectedKeys[0]"
            style="width: 188px; margin-bottom: 8px; display: block"
            @change="
              (e) => setSelectedKeys(e.target.value ? [e.target.value] : [])
            "
            @pressEnter="
              () => handleSearch(selectedKeys, confirm, column.dataIndex)
            "
          />
          <a-button
            type="primary"
            icon="search"
            size="small"
            style="width: 90px; margin-right: 8px"
            @click="() => handleSearch(selectedKeys, confirm, column.dataIndex)"
            >Search</a-button
          >
          <a-button
            size="small"
            style="width: 90px"
            @click="() => handleReset(clearFilters)"
            >Reset</a-button
          >
        </div>
        <a-icon
          slot="filterIcon"
          slot-scope="filtered"
          type="search"
          :style="{ color: filtered ? '#108ee9' : undefined }"
        />-->

        <a-table
          slot="expandedRowRender"
          :columns="innerColumns"
          :data-source="innerData"
          :rowKey="'id'"
          :pagination="false"
          size="small"
          class="innerthis"
        >
          <a
            :href="record.url"
            target="_blank"
            slot="url"
            slot-scope="text, record"
            >{{ text }}</a
          >
        </a-table>
      </a-table>
    </div>
  </div>
</template>

<script>
import buct from "../components/common/buct";
const columns = [
  {
    title: "主题",
    dataIndex: "theme",
    scopedSlots: {
      filterDropdown: "filterDropdown",
      filterIcon: "filterIcon",
    },
    onFilter: (value, record) => {
      return record.theme.includes(value);
    },
    width: 300,
  },
  {
    title: "作者",
    dataIndex: "author",
    scopedSlots: {
      filterDropdown: "filterDropdown",
      filterIcon: "filterIcon",
    },
    onFilter: (value, record) => {
      return record.author.includes(value);
    },
    width: 150,
  },
  {
    title: "关键词",
    dataIndex: "keyword",
    scopedSlots: {
      filterDropdown: "filterDropdown",
      filterIcon: "filterIcon",
    },
    onFilter: (value, record) => {
      return record.keyword.includes(value);
    },
    width: 300,
  },

];
const innerColumns = [
  {
    title:"网址",
    dataIndex: "url",
    scopedSlots: { customRender: "url" },
  },
  {
    title:"摘要",
    dataIndex: "abstract",
    ellipsis: true,
  },
];
export default {
  name: "paperIntroduction",
  data() {
    return {
      subBackgroundImageUrl: "",
      isTableShow: false,
      pagination: { pageSize: 5 },
      data: [],
      innerData: [],
      expandedRowKeys: [],
      columns,
      innerColumns,
    };
  },
  components: {
    buct,
  },
  methods: {
    returnIndex(){
      this.$router.push("/mainMenu");
    },
    rowClassName(record, index) {
      let className = "even-row";
      if (index % 2 === 1) className = "odd-row";
      return className;
    },
    expand(expanded, record) {
      //console.log(expanded,record)
      this.expandedRowKeys = [];
      if (expanded) {
        this.pagination= { pageSize: 4 },
        this.innerData = [];
        this.expandedRowKeys = [record.id];
        this.innerData.push(this.data.find((item) => item.id == record.id));
      }
      else{
        this.pagination= { pageSize: 5 }
      }
    },
    getBackendData() {
      this.$store.state.paperIntroductionDataGet(this.name).then((response) => {
        this.data = response.data;
        this.isTableShow = true;
      });
    },
    handleSearch(selectedKeys, confirm, dataIndex) {
      //console.log(selectedKeys, confirm, dataIndex)
      confirm();
    },
    handleReset(clearFilters) {
      clearFilters();
    },
  },
  mounted() {
    this.subBackgroundImageUrl = require("../assets/subBackgroundImage.png");
    this.getBackendData();
  },
};
</script>

<style>
.subBackgroundImage {
  background-position: center top;
  background-repeat: no-repeat;
  width: 100vw;
  height: 100vh;
  background-size: 100vw 100vh;
}
.even-row {
  background-color: rgba(165, 166, 255, 100) !important;
  height: 88px;
}
.odd-row {
  background-color: rgba(165, 208, 255, 100) !important;
  height:88px;
}
.menuHoverStyle:hover{
  cursor: pointer;
}
.this .ant-table-thead > tr > th{
  background-color:rgba(224, 174, 241, 51) !important;
  height:80px;
  padding:20px;
}
.this .ant-table-thead > tr > th > span{
  font-size: 25px;
  font-weight: bold;
  margin: 0 auto;
  position: relative;
  left:38%;
  /*margin-left:-30px;*/
}
.innerthis .ant-table-thead > tr > th{
  background-color:#FFF !important;
  height:0;
}
.innerthis .ant-table-thead > tr > th > span{
  font-size: 15px;
  font-weight: bold;
  margin: 0 auto;
  position: relative;
  left:40%;
  /*margin-left:-30px;*/
}
.that .ant-table-thead > tr > th{
  background-color:rgba(224, 174, 241, 51) !important;
  height:80px;
  padding:20px;
}
.that .ant-table-thead > tr > th > span{
  font-size: 25px;
  font-weight: bold;
  margin: 0 auto;
  position: relative;
  left:calc(50% - 45px);
  /*margin-left:-30px;*/
}
.ant-table-tbody > tr > td{
  text-align: center;
}

.ant-table-tbody > tr > td > .ant-table-row-expand-icon{
  display:none;
}
</style>