<template>
  <div>
    <div
      :style="{ backgroundImage: `url(${subBackgroundImageUrl})` }"
      class="subBackgroundImage"
    >
      <buct></buct>
      <div
        style="
          font-size: 50px;
          font-weight: bold;
          font-family: Tahoma;
          position: absolute;
          left: 5%;
          top: 5%;
        "
        class="menuHoverStyle"
        @click="returnIndex"
      >
        MENU
      </div>
      <div style="">
        <div
          :style="{
            height: '250px',
            width: '400px',
            position: 'absolute',
            left: '70%',
            top: '30%',
          }"
          :class="subClass[index]"
          v-for="(item, index) in [
            'subPie0',
            'subPie1',
            'subPie2',
            'subPie3',
            'subPie4',
          ]"
          :key="index"
          :id="item"
        ></div>
        <div
          :style="{
            height: '250px',
            width: '400px',
            position: 'absolute',
            left: '1%',
            top: '30%',
          }"
          :class="subClass[index + 5]"
          v-for="(item, index) in ['subPie5', 'subPie6', 'subPie7', 'subPie8']"
          :key="index"
          :id="item"
        ></div>
        <div
          id="main"
          :style="{
            height: '600px',
            width: '800px',
            position: 'absolute',
            left: '50%',
            marginLeft: '-400px',
            top: '50%',
            marginTop:'-300px',
            display: 'inline-block',
          }"
        ></div>
      </div>
    </div>
  </div>
</template>

<script>
import buct from "../components/common/buct";
export default {
  name: "statistic",
  data() {
    return {
      data: [],
      subBackgroundImageUrl: "",
      names: [],
      subClass: [],
      ids: [],
    };
  },
  components: {
    buct,
  },
  methods: {
    returnIndex() {
      this.$router.push("/mainMenu");
    },
    getBackendData() {
      this.$store.state
        .statisticDataGet(this.$route.query.cid)
        .then((response) => {
          this.data = response.data;
          this.names = [];
          for (let item of this.data) {
            this.names.push(item.name);
          }
          for (let i = 0; i < this.data.length; i = i + 1) {
            this.subClass.push("subPieHide");
            this.ids.push(`subPie${i}`);
          }
          this.$nextTick(() => {
            // 由于数据变化而重新渲染dom时调用，避免id还未被渲染就进行getElementById导致的错误
            this.drawLine();
          });
        });
    },
    drawLine() {
      let main = this.$echarts.init(document.getElementById("main"));
      let totals = [];
      for (let i = 0; i < this.data.length; i = i + 1) {
        totals.push({ value: this.data[i].total, name: this.names[i] });
      }
      let mainoption = {
        title: {
          //text: '数据来源',
          left: "center",
          top: "80px",
          textStyle: {
            fontSize: 30,
          },
        },
        tooltip: {
          trigger: "item",
          formatter: "{b} : {c} ({d}%)",
        },
        color: [
          "rgb(161,214,113)",
          "rgb(229,198,104)",
          "rgb(222,134,173)",
          "rgb(87,161,244)",
          "rgb(114,117,197)",
          "rgb(188,146,188)",
          "rgb(157,111,196)",
          "rgb(143,218,192)",
          "rgb(206,179,179)",
        ],
        series: [
          {
            type: "pie",
            radius: "75%",
            center: ["50%", "50%"],
            hoverOffset: 40,
            selectedMode: "single",
            data: totals,
            emphasis: {
              itemStyle: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: "rgba(0, 0, 0, 0.5)",
              },
            },
            label: {
              normal: {
                show: true,
                formatter: function (data) {
                  switch (data.dataIndex) {
                    case 0:
                      return `{i0|${data.name}}`;
                    case 1:
                      return `{i1|${data.name}}`;
                    case 7:
                      return `{i7|${data.name}}`;
                    case 8:
                      return `{i8|${data.name}}`;
                    default:
                      return `{normal|${data.name}}`;
                  }
                },
                rich: {
                  normal: {
                    fontSize: 15,
                    fontWeight: "bold",
                  },
                  i0: {
                    fontSize: 15,
                    fontWeight: "bold",
                    color: "#40713E",
                  },
                  i1: {
                    fontSize: 15,
                    fontWeight: "bold",
                    color: "#857A4E",
                  },
                  i7: {
                    fontSize: 15,
                    fontWeight: "bold",
                    color: "#0B8A81",
                  },
                  i8: {
                    fontSize: 15,
                    fontWeight: "bold",
                    color: "#946c85",
                  },
                },
              },
            },
          },
        ],
      };
      main.setOption(mainoption);

      let that = this;
      main.on("click", function (params) {
        for (let i = 0; i < that.data.length; i = i + 1) {
          that.subClass.splice(i, 1, "subPieHide");
        }
        that.subClass.splice(params.dataIndex, 1, "subPieShow");
      });

      let subs = [];
      for (let i = 0; i < this.data.length; i = i + 1) {
        subs.push(this.$echarts.init(document.getElementById(`subPie${i}`)));
      }

      let datas = [];
      for (let i = 0; i < this.data.length; i = i + 1) {
        datas.push([0, 0, 0]);
        datas[i][0] = this.data[i].negative;
        datas[i][1] = this.data[i].neutral;
        datas[i][2] = this.data[i].positive;
      }
      let suboption = [];
      for (let i = 0; i < this.data.length; i = i + 1) {
        let suboption_son = {
          title: {
            text: this.data[i].name,
            left: "center",
          },
          tooltip: {
            trigger: "item",
            formatter: "{b} : {c} ({d}%)",
          },
          legend: {
            // orient: 'vertical',
            // top: 'middle',
            bottom: 10,
            left: "center",
            data: ["negative", "neutral", "positive"],
          },
          color: ["rgb(203,182,206)", "rgb(237,205,158)", "rgb(175,173,186)"],
          series: [
            {
              type: "pie",
              radius: "50%",
              center: ["50%", "50%"],
              selectedMode: "single",
              data: [
                { value: datas[i][0], name: "negative" },
                { value: datas[i][1], name: "neutral" },
                { value: datas[i][2], name: "positive" },
              ],
              emphasis: {
                itemStyle: {
                  shadowBlur: 10,
                  shadowOffsetX: 0,
                  shadowColor: "rgba(0, 0, 0, 0.5)",
                },
              },
              label: {
                fontSize: 15,
                fontWeight: "bold",
              },
            },
          ],
        };
        suboption.push(suboption_son);
      }
      for (let i = 0; i < this.data.length; i = i + 1) {
        subs[i].setOption(suboption[i]);
      }
    },
  },
  beforeMount() {},
  mounted() {
    this.subBackgroundImageUrl = require("../assets/subBackgroundImage.png");
    this.getBackendData();
  },
};
</script>

<style scoped>
.subBackgroundImage {
  background-position: center top;
  background-repeat: no-repeat;
  width: 100vw;
  height: 100vh;
  background-size: 100vw 100vh;
}
.subPieShow {
  display: inline-block;
}
.subPieHide {
  display: none;
}
.menuHoverStyle:hover {
  cursor: pointer;
}
</style>